.embla {
  overflow: hidden;
}
.embla__container {
  display: flex;
}
.embla__slide {
  flex: 0 0 100%;
  min-width: 0;
}

.container {
  padding-left: constant(safe-area-inset-left);
  padding-right: constant(safe-area-inset-right);
}

.content {
  margin-top: 50px;
  margin-bottom: 50px;
  margin-bottom: calc(
    50px + constant(safe-area-inset-bottom)
  ); /* footer height + safe area */
  padding-top: 1rem;
  padding-bottom: 1rem;
  background: #b9f;
  color: white;
}
